.content {
  width: 85vw;
  margin: auto;
  border: 3px solid #eed8d8;
}
.content form {
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-bottom: 3px solid #d4c0c0;
  background-color: wheat;
}
.content form input {
  outline: none;
  border: 1px solid rgb(13, 177, 141);
  padding: 5px 10px;
}
.content form button {
  padding: 5px 10px;
  outline: none;
  border: none;
  background-color: rgb(12, 177, 149);
  color: #5c3c3c;
  font-weight: bold;
  cursor: pointer;
}
ul {
  padding: 0;
  margin: 0;
  list-style: none;
  background-color: #af6969;
}
.content section ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.content img {
  width: 14vw;
  height: 10vw;
  margin: 10px auto;
  display: flex;
  justify-content: center;
}
.content li {
  border: 4px solid #000;
  width: 16vw;
  margin: 25px;
  background-color: #eee;
  padding: 15px;
  border-radius: 20px;
}
.content li h2 {
  font-weight: normal;
  font-size: 1.3rem;
  margin-left: 18px;
}
.content li p {
  margin-left: 18px;
}
